import { Component, OnInit } from '@angular/core';
import { RealisationsService } from 'src/app/services/realisations.service';

@Component({
  selector: 'app-realisations',
  templateUrl: './realisations.component.html',
  styleUrls: ['./realisations.component.scss']
})
export class RealisationsComponent implements OnInit {

  realisations: any[];

  constructor(private realisationsService:  RealisationsService) { }

  ngOnInit(): void {
    this.realisations = this.realisationsService.getRealisations();
  }

}
